<template>
  <BaseLayout wrapperClass="resetPassword">
    <Breadcrumbs />
    <div class="container">
      <BRow class="forgot-password flex-column">
        <BCol md="10" lg="7" class="mobile-form-email">
          <h1>{{ $t("password_reset") }}</h1>
          <p>
            {{ $t("reset_password_text") }}
          </p>
          <BFormGroup class="account-inputs mb-6" :label="$t('form_email') + '*'" label-for="email">
            <BFormInput
              id="email"
              v-model="email"
              type="text"
              :state="emailState"
              class=""
              aria-describedby="email-feedback"
              :readonly="readOnlyEmail"
            ></BFormInput>
            <BFormInvalidFeedback id="email-feedback">
              {{ emailError }}
            </BFormInvalidFeedback>
          </BFormGroup>
          <div>
            <input
              id="token"
              v-model="token"
              type="hidden"
              :state="tokenState"
              aria-describedby="token-feedback"
            />
            <BFormInvalidFeedback id="token-feedback">
              {{ tokenError }}
            </BFormInvalidFeedback>
          </div>
          <BFormGroup
            class="account-inputs mb-6"
            id="password-group"
            :label="$t('form_password') + '*'"
            label-for="password"
          >
            <BFormInput
              id="password"
              v-model="password"
              type="password"
              :state="passwordState"
              aria-describedby="password-feedback"
            ></BFormInput>
            <BFormInvalidFeedback id="password-feedback">
              {{ passwordError }}
            </BFormInvalidFeedback>
          </BFormGroup>
          <BFormGroup
            class="account-inputs mb-6"
            id="password_confirm-group"
            :label="$t('form_password_confirmation') + '*'"
            label-for="password_confirm"
          >
            <BFormInput
              id="password_confirm"
              v-model="password_confirm"
              type="password"
              :state="password_confirmState"
              aria-describedby="password_confirm-feedback"
            ></BFormInput>
            <BFormInvalidFeedback id="password_confirm-feedback">
              {{ password_confirmError }}
            </BFormInvalidFeedback>
          </BFormGroup>
          <div>{{ description }}</div>
          <div class="btn-wrap d-flex justify-content-md-between align-items-md-center">
            <router-link class="back" to="/login">{{ $t("go_back") }}</router-link>
            <div class="btn-holder">
              <BButton @click="formSubmit" variant="info" class="text-white form-submit-btn">{{
                $t("reset_my_password")
              }}</BButton>
            </div>
          </div>
        </BCol>
      </BRow>
    </div>
  </BaseLayout>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";
import BaseLayout from "@/base/core/components/common/BaseLayout.vue";
import Breadcrumbs from "@/base/core/components/Breadcrumbs.vue";
import { resetPassword } from "@storefront/core/data-resolver/user";
import { useI18n } from "vue-i18n";
import { useHead } from "@unhead/vue";
import { useStore } from "vuex";

const route = useRoute();
const router = useRouter();
const { t } = useI18n();
const store = useStore();

const email = ref(route.query.email || "");
const emailState = ref(null);
const emailError = ref("");
const description = ref("");
const password = ref("");
const passwordState = ref(null);
const passwordError = ref("");
const password_confirm = ref("");
const password_confirmState = ref(null);
const password_confirmError = ref("");
const token = ref(route.query.token || "");
const tokenState = ref(null);
const tokenError = ref("");
const emailReg = new RegExp(
  /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/
);

const readOnlyEmail = computed(() => email.value.length > 0);

const validateEmail = () => {
  if (email.value === "") {
    emailState.value = false;
    emailError.value = t("email_is_required_field");
    return false;
  }
  if (!emailReg.test(email.value)) {
    emailState.value = false;
    emailError.value = t("email_is_invalid_field");
    return false;
  }
  emailState.value = true;
  return true;
};

const validatePassword = () => {
  if (password.value.length < 8) {
    passwordState.value = false;
    passwordError.value = t("password_needs");
    return false;
  } else if (!/[a-z]/.test(password.value)) {
    passwordState.value = false;
    passwordError.value = t("password_needs");
    return false;
  } else if (!/[!"#$%&'()*+,-.:;<=>?@[\]^_`{|}~]/.test(password.value)) {
    passwordState.value = false;
    passwordError.value = t("password_needs");
    return false;
  } else if (!/[A-Z]/.test(password.value)) {
    passwordState.value = false;
    passwordError.value = t("password_needs");
    return false;
  } else if (!/\d/.test(password.value)) {
    passwordState.value = false;
    passwordError.value = t("password_needs");
    return false;
  } else passwordState.value = true;
  return true;
};

const validateConfirmationPassword = () => {
  if (password.value !== password_confirm.value) {
    password_confirmState.value = false;
    password_confirmError.value = t("password_confirm_not_equal");
    return false;
  }
  password_confirmState.value = true;
  return true;
};

const validateToken = () => {
  if (token.value === "") {
    tokenState.value = false;
    tokenError.value = t("token_not_filled");
    return false;
  }
  tokenState.value = true;
  return true;
};
const formSubmit = async () => {
  if (validateEmail() && validatePassword() && validateConfirmationPassword() && validateToken()) {
    const retval = await resetPassword(email.value, token.value, password.value);
    if (retval === true) {
      description.value = t("password_resetted");
      router.push({ name: "login" });
    } else {
      description.value = t("password_not_resetted");
    }
  }
};

useHead({
  title: () => t("password_reset"),
  meta: [
    { name: "title", content: () => t("password_reset") },
    { name: "keywords", content: () => t("password_reset") },
    { name: "description", content: () => t("password_reset") },
  ],
});

onMounted(() => {
  const bcrumb = { current: t("password_reset"), routes: [] };
  store.commit("breadcrumbs/set", bcrumb);
});
</script>
<style scoped>
@import "@/base/core/components/login/login.scss";
</style>
